.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 300px;
  box-shadow: 0 3px 4px #999, 0 6px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:orange;
}
.card > .img-container > img {
  width: 60%;
  position: relative;
  top: 20px;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  /* padding-top: 50px; */
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  /* padding: 20px; */
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  font-family: 'Amatic SC', cursive;
  /* background-image: url("../../images/crayonBG.jpg"); */
  
}

.blurb {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: black;
  margin-bottom: 50px;
  margin-top: 150px;
}

.nav {
    background-color: orange;
    height:80px;
    position:fixed;
    z-index:2;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    width: 100%;
    margin-top: auto;
    padding-top: 11px;
    padding-right: 10px;
    text-align: right;
    font-size: -webkit-xxx-large;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

